import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
class Landing extends Component {
  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }
  render() {
    return (
      <div className="landing">
        <div className="landing-inner text-dark">
          <div className="container">
            <div className="row">
              <div className="col-md-12 text-center">
                <h1 className="display-3 mb-5 scrumCharmon">
                  Personal Project Management Tool
                </h1>
                <p className="Lead display-6 scrumFuzzyBubbles">
                  Login to your account to join active projects or start your
                  own!!!
                </p>
                <hr />
                <Link
                  className="btn mr-4 scrumBtn scrumUbuntu"
                  style={{ color: "#FF8C00" }}
                  to="/register"
                >
                  Sign Up
                </Link>
                <Link
                  className="btn mr-4 scrumSubmitBtn  scrumUbuntu"
                  to="/login"
                  style={{ color: "#A52A2A" }}
                >
                  Login
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Landing.propTypes = {
  security: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  security: state.security,
});

export default connect(mapStateToProps)(Landing);
